div#root {
    position: relative;
}
body:before {
    content: "";
    display: inline-block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0;
    background-color: rgba(255, 255, 255, 0.5);
}
.following li a img{
  height: 30px;
}
.following{
  margin-right: 50px;
}
.is-active{
  color: red;
}
.active{
  color: #000;
}
.tab:after,
.tab:before{
  display: none;
}
.tab:hover{
  border-left-color: #fff;
  border-right-color: #fff; 
}
.tab:before {
    content: '';
    position: absolute;
    left: -2px;
    bottom: -2px;
    z-index: 1;
    width: 0%;
    height: 2px;
    background-color: #fff;
    display: block;
    -webkit-transition: all 0.25s cubic-bezier(.5, .24, 0, 1);
    transition: all 0.25s cubic-bezier(.5, .24, 0, 1);
}
.tab:after {
    content: '';
    position: absolute;
    right: -2px;
    top: -2px;
    z-index: 1;
    width: 0%;
    height: 2px;
    background-color: #ffffff;
    -webkit-transition: all 0.25s cubic-bezier(.5, .24, 0, 1);
    transition: all 0.25s cubic-bezier(.5, .24, 0, 1);
}
